.MenuLogo{
    max-width: 120px;
    height: auto;
}
.MainMenu .desktopMenu{
    box-shadow: none;
}

.MainMenu .desktopMenu a{
    color: #172746;
    font-size: 18px;
    font-weight: 700;
    padding: 0px 16px;
    text-decoration: none;
    transition-duration: .4s;
}
.scroll_padding{
    padding: 2px 0px;
}
.MainMenu .desktopMenu a:hover{
    color: #00d1f9;
}
.togglebutton{
    position: absolute !important;
    right: 0;
}
.togglebutton .icon{
    color :#172746;
    font-size: 32px;
}
.mobileMenu{
    background-color: #061a3a;
}
.mobileMenu .mobilemenuitem{
    display: block;
    line-height: 24px;
    padding: 10px 25px;
    font-size: 15px;
    font-weight: 500;
    color: #fff;
    text-decoration: none;
    border-top: 1px solid #fff;
    text-align: left;
}
.headerShape::before{
    position: absolute;
    content: '';
    background-image: url('../images/headershape.png');
    background-size: cover;
    bottom: 0;
    width: 100%;
    height: 143px;
    bottom: -143px;
    background-size: 100% 100%;
}

.goto-top{
    background-color: #00d1f9;
    color: #fff;
    position: fixed;
    right: 30px;
    bottom: 30px;
    color: #fff;
    z-index: 9999;
    padding: 14px;
    border: none;
    border-radius: 8px;
    transition-duration: .5s;
}
