.footer {
    position: relative;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #1162aa;
    color: #fff;
    z-index: 0;
}

.footer::before {
    background-image: url('../images/footer-bg.png');
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: contain;
    content: " ";
    display: block;
    height: 100%;
    left: 0;
    opacity: .1;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1;
}

.footershape {
    background-image: url('../images/footershape.png');
    width: 100%;
    height: 100px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 100px;
}

.footertop span {
    color: #00d1f9;
}

.contact-btn {
    background-color: #00d1f9;
    padding: 20px 40px;
    color: #fff;
    text-decoration: none;
    opacity: 0.9;
    font-size: 20px;
    font-weight: 600;
}
.footertop .heading{
    font-weight: 700;
}
.contact-btn:hover {
    color: #fff;
    opacity: 1;
}

.footermain .icon {
    color: #00d1f9 !important;
}

.footermain h4{
    font-weight: 600;
}

.footermain p{
    font-size: 18px;
}

.footer-email-inp {
    height: 50px;
}

.footer-email-inp .arw {
    background-color: #00d1f9;
    color: #fff;
    cursor: pointer;
}

.useful-links a {
    color: #fff;
    text-decoration: none;
    display: inline-block;
    font-size: 18px;
}

.useful-links a:hover {
    color: #00d1f9;
}


.footermain .socail-icon {
    background: rgba(255, 255, 255, 0.15);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    transition-duration: .5s;
    margin-right: 12px;
    position: relative;
}

.footer .socail-icon a {
    color: #fff;
    position: absolute;
    width: 100%;
    height: 100%;
}

.footermain .socail-icon:hover {
    background-color: #00d1f9;
}

.footerbottom {
    background-color: #094a84;
}